.nav {
  display: flex;
  align-items: center;
  flex: 2 1 auto;
  margin-left: 56px;

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__item {
    max-width: 200px;

    @include big-desktop {
      max-width: 180px;
    }

    &:not(:last-child) {
      margin-right: 48px;

      @include big-desktop {
        margin-right: 40px;
      }
    }
  }

  &__link {
    font-weight: 700;
    font-size: 15px;
    line-height: 17px;
    color: var(--light-color);
  }
}
