.compare {
  padding-bottom: 174px;

  @include tablet {
    padding-bottom: 157px;
  }


  &__container {
    background-image: url(../img/ogn.png);
    background-size: 597px 698px;
    background-position: center 95px;
    background-repeat: no-repeat;

    @include big-desktop {
      background-size: 460px 539px;
      background-position: center 130px;
    }

    @include tablet {
      background-size: 272px 320px;
      background-position: center 155px;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    position: relative;
    padding-bottom: 35px;
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 39px;

    @include big-desktop {
      margin-bottom: 54px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 28px;
    }
  }

  &__left {

    & .compare__item {
      &::before {
        content: '';
        position: absolute;
        bottom: -22px;
        left: 0;
        width: 150%;
        height: 3px;
        background: linear-gradient(90deg, #2D3A94, #51ABE3);
        z-index: -1;
        opacity: 0.5;

        @include tablet {
          bottom: -8px;
        }
      }

      @include big-desktop {
        &:nth-child(n+5) {
          &::after {
            content: '';
            position: absolute;
            bottom: 87px;
            right: -416px;
            width: 100%;
            height: 3px;
            background: linear-gradient(90deg, #51ABE3, #2D3A94);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(-45deg);
          }
        }
      }
    }
  }

  &__list {}

  &__list-title {
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    text-transform: uppercase;
    color: var(--title-color);
    margin: 0;
    margin-bottom: 42px;

    @include tablet {
      margin-bottom: 5px;
    }
  }

  &__item {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: var(--primary-color);
    position: relative;

    @include big-desktop {
      font-size: 16px;
      line-height: 18px;
      display: flex;
      align-items: center;
    }

    &:not(:last-child) {
      margin-bottom: 52px;

      @include big-desktop {
        margin-bottom: 37px;
      }

      @include tablet {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 15px;
      }
    }
  }

  &__item--bold {
    font-weight: 700;
    font-size: 24px;
    padding-left: 20px;

    @include big-desktop {
      font-size: 22px;
    }

    @include tablet {
      padding-left: 0;
      font-size: 20px;
    }
  }

  &__item-name {
    @include big-desktop {
      max-width: 160px;
    }

    @include tablet {
      max-width: 100%;
      margin-bottom: 5px;
      font-size: 12px;
      line-height: 21px;
    }
  }

  &__right {
    text-align: right;

    & .compare__item {

      @include big-desktop {
        justify-content: flex-end;
      }

      @include tablet {
        align-items: flex-end;
      }

      &::before {
        content: '';
        position: absolute;
        bottom: -22px;
        right: 0;
        width: 150%;
        height: 3px;
        background: linear-gradient(90deg, #FCC009, #F14624);
        z-index: -1;
        opacity: 0.5;

        @include tablet {
          bottom: -8px;
        }
      }

      @include big-desktop {
        &:nth-child(n+5) {
          &::after {
            content: '';
            position: absolute;
            bottom: 91px;
            left: -433px;
            width: 100%;
            height: 3px;
            background: linear-gradient(90deg, #F14624, #FCC009);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(45deg);
          }
        }
      }
    }
  }
}
